[type="radio"]:checked,
[type="radio"]:not(:checked) {
  height: 0;
  width: 0;
  cursor: pointer;
  opacity: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label
{
    position: relative;
    padding-left: 1.5rem;
    cursor: pointer;
    line-height: 1rem;
    display: inline-block;
    color: white;
}
[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: -0.1rem;
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 100%;
    background: #fff;
}
[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
    content: '';
    width: 0.8rem;
    height: 0.8rem;
    background: #bf6068;
    background: var(--alert);
    position: absolute;
    top: 0.1rem;
    left: 0.2rem;
    border-radius: 100%;
    transition: all 0.2s;
}
[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    transform: scale(0);
}
[type="radio"]:checked + label:after {
    opacity: 1;
    transform: scale(1);
}
